package com.cordylussoftware.nocta.components.elements

import androidx.compose.runtime.Composable
import com.cordylussoftware.nocta.utils.Res
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.css.TextDecorationLine
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.css.px

@Composable
fun LinkText(modifier: Modifier = Modifier, text: String, link: String = "") {
    Link(path = link) {
        SpanText(
            text = text,
            modifier = modifier
                .fontFamily(Res.Fonts.ROBOTO_REGULAR)
                .color(Res.Theme.BLUE.color)
                .fontSize(16.px)
                .fontWeight(FontWeight.Normal)
                .textAlign(TextAlign.Start)
                .textDecorationLine(textDecorationLine = TextDecorationLine.Underline)
        )
    }
}